
:root {
    --dark_blue: #1A2238;
    --light_blue: #073F80;
    --pink: #ff00d4;
}

.PageContainer {
    position: fixed;
    max-width: 360px;
    height: 100%;
    width: 360px;
    margin-left: 0;
    background: linear-gradient(169.06deg, var(--dark_blue) 26.78%, var(--pink) 132.23%);
    transform: scale(1);
    overflow: hidden;
    transition: 
        scale 0.5s,
        margin-left 0.5s,
        border-radius 0.5s ease-in-out;
}

.PageContainer.MenuOpen.Overlay {
    opacity: 0%;
    z-index: 5;
} .PageContainer.MenuClosed.Overlay {
    opacity: 0%;
    z-index: -12;
} 

.PageContainer.Underlay{
    background: whitesmoke;
    opacity: .5;
} .PageContainer.MenuClosed.Underlay {
    scale: 1;
    opacity: 0;
    border-radius: 0px;
    margin: 0;
} .PageContainer.MenuOpen.Underlay {
    scale: .7;
    opacity: .5;
    border-radius: 60px 0px 0px 60px;
    margin-left: calc(40% - 20px);
    margin-top: 10px;
}

.PageContainer.MenuClosed {
    scale: 1;
    border-radius: 0px;
    margin-left: calc(0%);
}

.PageContainer.MenuOpen {
    scale: .7;
    border-radius: 60px 0px 0px 60px;
    margin-left: calc(40%);
}

.PageContainer.SlideOut {
    transition: 0s;
    animation: Page_SlideOut_anim 1s ease-in-out;
    transform: translateX(400px) scale(.7);
    opacity: 0%;
} .PageContainer.SlideIn {
    /* transition: 0s; */
    transform: translateX(0px) scale(1);
    opacity: 100%;
    animation: Page_SlideIn_anim 2s ease-in-out;
} 

/* Todo delay this animation so that slideout finishes first */
@keyframes Page_SlideIn_anim {
    0%{
        transform: translateX(400px) scale(.7);
        border-radius: 60px 60px 60px 60px;
    }
    40% {
        transform: translateX(400px) scale(.7);
    }
    55% {
        transform: translateX(0px) scale(.7);
    }
    65% {
        transform: scale(.55);
    }
}

@keyframes Page_SlideOut_anim {
    0% {
        transform: translateX(0px) scale(.7);
        margin-left: calc(40%);
        border-radius: 60px;
    }
    39% {
        opacity: 100;
    }
    40% {
        transform: translateX(400px) scale(.7);
        opacity: 0;
        margin-left: calc(40%);
    }
}